import Plot from 'react-plotly.js'

const Plots = ({plotData, assay}) => {
  const plots = [
    {
      title: "Medeltäckning",
      assays: ['all'],
      yaxis: {
        title: "X coverage"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: { 
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.mean_coverage)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      title: "Fragmentstorlek", 
      assays: ['all'],
      yaxis: {
        title: "base pairs"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseInt(point.qc.ins_size)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      title: "Duplikation mängd",
      assays: ['all'],
      yaxis: {
        title: "% duplication"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.dup_pct)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      title: "GC-drop",
      assays: ['wgs'],
      yaxis: {
        title: "%"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.gc_drop)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      key: 5,
      title: "AT-drop",
      assays: ['wgs'],
      yaxis: {
        title: "%"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.at_drop)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      title: "Felfrekvens",
      assays: ['wgs'],
      yaxis: {
        title: "%"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.pf_error_rate)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
    {
      title: "Kontamination",
      assays: ['exome'],
      yaxis: {
        title: "%"
      },
      xaxis: {
        title: "Datum"
      },
      plotData: {
        plotType: "scatter", 
        mode: "markers",
        x: plotData.map((point) => {return point.creation_date}), 
        y: plotData.map((point) => {return parseFloat(point.qc.contamination)}),
        text: plotData.map((point) => {
          return `sample id: ${point['sample-id']}; sub-assay: ${point.subassay}`
        })
      }
    },
  ]
  return (
    plots.map( (plot) => ( 
      (plot.assays.includes('all') || plot.assays.includes(assay) ) &&
      <div className="plot">
        <Plot 
          data={[plot.plotData]} 
          layout={ {title: plot.title, xaxis: plot.xaxis, yaxis: plot.yaxis} }
        />
      </div>
    ))
  )
}

Plots.defaultProps = {
  plotData: []
}

export default Plots
