import React from 'react'
import PropTypes from 'prop-types'
import Select from './Select'
import Button from './Button'

const DataSelection = ({assayNames, onChange, onClick, selectedAssay, selectedTimeInterval}) => {
  const names = ['test']
  const months = -12
  const timeRanges = [
    {title: "All", monthDelta: null},
    {title: "3 Years", monthDelta: 3 * months},
    {title: "1 Year", monthDelta: months},
    {title: "6 Mon", monthDelta: 0.5 * months},
  ]

  return (
    <div className='data-selection'>
      <Select 
      classNames={names} 
      onChange={onChange} 
      options={assayNames}
      selectedOption={selectedAssay}
      />
      {timeRanges.map((btn) => (
        <Button 
          text={btn.title}
          onClick={() => {onClick(btn.monthDelta)}}
          disabled={btn.monthDelta === selectedTimeInterval}
        />
      ))}
    </div>
  )
}

DataSelection.propTypes = {

}

export default DataSelection
