import DataSelection from "./components/DataSelection"
import Plots from "./components/Plots"
import { get } from './fetch'

import { useState, useEffect } from 'react'
import { getAllByPlaceholderText } from "@testing-library/react"

const apiUrl = 'http://mtlucmds1.lund.skane.se/cdm/api'
//const apiUrl = 'http://localhost:3010'

const fetchAssayNames = async () => {
  const res = await fetch(`${apiUrl}/assays`)
  return res.json()
}

const fetchSamples = async (params) => {
  const res = await get(
    `${apiUrl}/samples`, params)
  return res
}

const App = () => {
  // get query parameters from url
  const queryParams = new URLSearchParams(window.location.search)

  const [assayInfo, setAssayInfo] = useState(null)
  const [sampleData, setSampleData] = useState(null)
  const [selectedAssay, setSelectedAssay] = useState(queryParams.get('assay'))
  const [selectedTimeInterval, setSelectedTimeInterval] = useState(null)

  // fetch assay info from database
  useEffect(() => {
    const getAssayInfo = async () => {
      const data = await fetchAssayNames()
      setAssayInfo(data)
    }
    getAssayInfo()
  }, [])

  // fetchSampleData
  const getSamplesInfoFromEvent = async (event) => {
    const assay = event.target.value
    setSelectedAssay(assay)
    getSamplesInfo(assay)
  }

  const getSamplesInfo = async (assay) => {
    const data = await fetchSamples({
      assay: assay, 
      has_qc: true,
    })
    setSampleData(data)
  }

  const getSampleInfoTimeDelta = async (months) => {
    setSelectedTimeInterval(months)
    const data = await fetchSamples({
      assay: selectedAssay, has_qc: true,
    })
    if ( months === null ) {
      setSampleData(data)
    } else {
      // calculate the date x month ago
      const timeLimit = new Date()
      timeLimit.setMonth(timeLimit.getMonth() + months)
      // filter relevant samples
      setSampleData(data.filter((sample) => {
        return timeLimit < new Date(sample.creation_date)
      }))
    }
  }

  // If an assay is specified, display plots right away
  useEffect(() => {
    selectedAssay !== null && getSamplesInfo(selectedAssay)
  }, [])

  return (
    <div className='container'>
      <h2>Quality trends{ selectedAssay !== null && `: ${selectedAssay}`}</h2>
      <DataSelection 
      assayNames={assayInfo} 
      onChange={getSamplesInfoFromEvent} 
      onClick={getSampleInfoTimeDelta} 
      selectedAssay={selectedAssay}
      selectedTimeInterval={selectedTimeInterval}/>
      {sampleData !== null ? 
        <Plots plotData={sampleData} assay={selectedAssay}/> : 
        <div>Please select an assay to show plots</div>
      }
    </div>
  )
}

export default App
